import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import Sound from 'react-sound'
import './app.css'
import { startAudio, startPlaying, stopPlaying, pausePlaying,
         previousSlide, nextSlide, getTiles } from '../statemanagement'

class Label extends PureComponent {
  render() { return <span>Status: {this.props.status}</span>  }
}

const omit = (obj, omitKey) => {
  return Object.keys(obj).reduce((result, key) => {
    if(key !== omitKey) {
       result[key] = obj[key]
    }
    return result
  }, {})
}

class TouchButton extends PureComponent {

  constructor(props) {
    super(props)
    this.onEvent = this.onEvent.bind(this)
    this.touchEnd = this.touchEnd.bind(this)
    this.filteredProps = omit(this.props, 'onEvent')
  }

  onEvent(e) {
    e.preventDefault()
    e.stopPropagation()
    this.props.onEvent()
  }

  touchEnd(e) {
    e.preventDefault()
    e.stopPropagation()
  }

  render() {
    return <button onMouseDown={this.onEvent}
                   onTouchStart={this.onEvent}
                   onTouchEnd={this.touchEnd}
                   {...this.filteredProps}
           />
  }
}

class Audio extends PureComponent {

  constructor(props) {
    super(props)
    this.stop = this.stop.bind(this)
  }

  stop()  { this.props.dispatch(stopPlaying()) }

  render() {
    return (
        <Sound
          url={this.props.audio}
          playStatus={this.props.status}
          onFinishedPlaying={this.stop}
        />
    )
  }
}

class AudioControl extends PureComponent {

  constructor(props) {
    super(props)
    this.start = this.start.bind(this)
    this.stop  = this.stop.bind(this)
    this.pause = this.pause.bind(this)
  }

  start() { this.props.dispatch(startPlaying()) }
  stop()  { this.props.dispatch(stopPlaying())  }
  pause() { this.props.dispatch(pausePlaying()) }

  render() {
    return (
      <div id="audio_control">
        <button onClick={this.start}>Start</button> {' '}
        <button onClick={this.pause}>Pause</button> {' '}
        <button onClick={this.stop}>Stop</button> {' '}
      </div>
    )
  }
}

class SlideControl extends PureComponent {

  constructor(props) {
      super(props)
      this.prev = this.prev.bind(this)
      this.next = this.next.bind(this)
  }

  prev() {
    this.props.dispatch(stopPlaying())
    this.props.dispatch(previousSlide())
  }

  next() {
    this.props.dispatch(stopPlaying())
    this.props.dispatch(nextSlide())
  }

  render() {
    return (
      <div>
        <TouchButton onEvent={this.prev} className="floatLeft">{" Elz"}</TouchButton>
        <TouchButton onEvent={this.next} className="floatRight">{" Kvetkez"}</TouchButton>
      </div>
    )
  }
}

class Tile extends PureComponent {

  constructor(props) {
    super(props)
    this.start = this.start.bind(this)
  }

  start() { this.props.dispatch(startAudio(this.props.audio)) }

  render() { return tile(this.props.image, this.props.text, this.start) }
}

const tile = (imgsrc, text, cb) =>
  <div className='tile' onMouseDown={cb} onTouchStart={cb}>
    <img src={imgsrc} alt={text} />
    <span className='img_text'>{text}</span>
  </div>

const App = ({ status, audio, index, dispatch }) =>
  <div className="container">
    <header>
      <SlideControl dispatch={dispatch} />
      <Audio status={status} audio={audio} dispatch={dispatch} />
    </header>
    {getTiles(index).map(t => (<Tile {...t} key={t.image} dispatch={dispatch} />))}
    <footer>
      <AudioControl dispatch={dispatch} />
      <Label status={status} />
    </footer>
  </div>

//==============================================================================

export default connect(state => state)(App)



