const all_tiles = [
  {text: '',  image: './images/rum.jpg', audio: './audio/a.m4a'},
  {text: '',  image: './images/CIMG3993.JPG', audio: './audio/b.m4a'},
  {text: '',  image: './images/CIMG4616.JPG', audio: './audio/c.m4a'},
  {text: '',  image: './images/CIMG4631.JPG', audio: './audio/d.m4a'},
  {text: '',  image: './images/CIMG4699.JPG', audio: './audio/e.m4a'},
  {text: '',  image: './images/jw-colors.jpg', audio: './audio/f.m4a'},
  {text: '',  image: './images/IMG_1153.JPG', audio: './audio/g.m4a'},
  {text: '',  image: './images/IMG_1181.JPG', audio: './audio/h.m4a'},
  {text: '',  image: './images/IMG_9156.JPG', audio: './audio/i.m4a'},
  {text: '',  image: './images/IMG_6529.JPG', audio: './audio/j.m4a'},
  {text: '',  image: './images/Gergo.JPG', audio: './audio/k.m4a'},
  {text: '',  image: './images/Lillusk.JPG', audio: './audio/l.m4a'},
  {text: '',  image: './images/Piroska04.JPG', audio: './audio/m.m4a'},
  {text: '',  image: './images/gyertyam.jpg', audio: './audio/n.m4a'},
  {text: '',  image: './images/Lillus.jpeg', audio: './audio/o.m4a'},
  {text: '',  image: './images/p1010057.jpg', audio: './audio/p.m4a'},
  {text: '',  image: './images/p1010042.jpg', audio: './audio/q.m4a'},
  {text: '',  image: './images/jw-colors.jpg', audio: './audio/r.m4a'}
]

const N_CHUNKS = Math.ceil(all_tiles.length / 6)

export const getTiles = (index) => all_tiles.slice(index, index + 6)

const increment = (index) => (index + 6) % (6*N_CHUNKS)

const decrement = (index) => {
  index -= 6
  if (index < 0) {
    index += all_tiles.length
  }
  return index % (6*N_CHUNKS)
}

//------------------------------------------------------------------------------

export const initialState = {
  status: Sound.status.STOPPED,
  audio: '',
  index: 0,
}

const AUDIO_START = 'AUDIO_START'
const STATUS_CHANGE = 'STATUS_CHANGE'
const NEXT_SLIDE = 'NEXT_SLIDE'
const PREVIOUS_SLIDE = 'PREVIOUS_SLIDE'


export const startAudio = (audio) => ({
  type: AUDIO_START,
  payload: { status: Sound.status.PLAYING, audio: audio }
})

export const startPlaying = () => ({
  type: STATUS_CHANGE,
  payload: Sound.status.PLAYING
})

export const stopPlaying = () => ({
  type: STATUS_CHANGE,
  payload: Sound.status.STOPPED
})

export const pausePlaying = () => ({
  type: STATUS_CHANGE,
  payload: Sound.status.PAUSED
})

export const nextSlide = () => ({
  type: NEXT_SLIDE
})

export const previousSlide = () => ({
  type: PREVIOUS_SLIDE
})

//------------------------------------------------------------------------------

const rootReducer = (state, action) => {
  switch (action.type) {
    case STATUS_CHANGE:
      return { ...state, status: action.payload }
    case AUDIO_START:
      // Don't start playing if we are already playing
      return state.status===Sound.status.PLAYING ? state : { ...state, ...action.payload }
    case NEXT_SLIDE:
      return { ...state, index: increment(state.index)}
    case PREVIOUS_SLIDE:
      return { ...state, index: decrement(state.index)}
    default:
      return state
  }
}

export default rootReducer



