import React from 'react'
import ReactDOM from 'react-dom'
import { createStore } from 'redux'
import { Provider } from 'react-redux'
import rootReducer, { initialState } from './statemanagement'
import App from './components/App'

/* global soundManager:false */
import 'react-sound';
soundManager.setup({debugMode: false});

// The alternative would be to hack react-sound so that the stupid
// soundmanager2 does not log to the console:
// soundManager = require('soundmanager2/script/soundmanager2-nodebug-jsmin.js').soundManager;
// See also: https://github.com/leoasis/react-sound/issues/13

const enhancer = process.env.NODE_ENV !== 'production' ?
                 (
                   window.__REDUX_DEVTOOLS_EXTENSION__ &&
                   window.__REDUX_DEVTOOLS_EXTENSION__()
                 ) : undefined

const store = createStore(
  rootReducer,
  initialState,
  enhancer
)

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root')
)



